define('dummy/views/editable-table-cell', ['exports', 'ember', 'dummy/views/table-cell'], function (exports, Ember, TableCell) {

  'use strict';

  exports['default'] = TableCell['default'].extend({
    className: "editable-table-cell",
    templateName: "editable-table/editable-table-cell",
    isEditing: false,
    type: "text",

    innerTextField: Ember['default'].TextField.extend({
      typeBinding: "parentView.type",
      valueBinding: "parentView.cellContent",
      didInsertElement: function () {
        return this.$().focus();
      },
      focusOut: function () {
        return this.set("parentView.isEditing", false);
      }
    }),

    onRowContentDidChange: Ember['default'].observer(function () {
      return this.set("isEditing", false);
    }).observes("row.content"),

    click: function (event) {
      this.set("isEditing", true);
      return event.stopPropagation();
    }
  });

});